.inner_container::before{
    content:"";
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #D10024;
    opacity: 0.8;
    -webkit-clip-path: polygon(46% 0, 87% 0, 41% 100%, 0 100%, 0 0);
            clip-path: polygon(46% 0, 87% 0, 41% 100%, 0 100%, 0 0);
    z-index:0;
    transition: -webkit-clip-path .5s ease;
    transition: clip-path .5s ease;
    transition: clip-path .5s ease, -webkit-clip-path .5s ease;
}

.inner_container:hover:before{
    -webkit-clip-path: polygon(46% 0, 100% 0, 100% 100%, 0 100%, 0 0);
            clip-path: polygon(46% 0, 100% 0, 100% 100%, 0 100%, 0 0);
}

