.parallax {
    /* The image used */
  margin-top:200px;
    /* Set a specific height */
    min-height: 500px; 
  
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index:0;
    -webkit-filter: blur(3px);
            filter: blur(3px);

 }

