ul, ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

.section-tab-nav li {
    display: inline-block;
    margin-right: 15px;
}

.section-tab-nav > li > a {
    font-weight: 700;
    color: #8D99AE;

}

section-tab-nav li a {
    color: #D10024;
}

.section-tab-nav li a:hover:after, .section-tab-nav li a:focus:after, .section-tab-nav li.active a:after {
    width: 100%;
}

.section-tab-nav li a:after {
    content: "";
    display: block;
    width: 0%;
    height: 2px;
    background-color: #D10024;
    transition: 0.2s all;
}

